@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-main {
    background-color: v.$gris;
    width: 100%;
    height: 100vh;
}

.portfolio-return {
    top: 2rem;
    background-color: transparent;
    border-radius: 24px;
    border: 1px v.$azul solid;
    padding: 1rem 2rem;
    position: relative;
    margin: 0 auto;
    overflow: hidden;
    span {
        color: v.$azul;
    }

    font-weight: 500;
    cursor: pointer;
    transition: all .3s;

    .arrow-btn {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        left: 0px;
        top: 0;
        width: 100%;
        opacity: 1;
        height: 100%;
        transform: translateX(110%);
        transition: transform .4s ease;
        background-color: v.$azul;
        border-radius: 24px;
        color: v.$blanco;
    }

    &:hover {
        .arrow-btn{
            transform: translateX(0%);
        }
        .texto-btn {
            opacity: 0;
        }
    }
}

.contenedor-credenciales {
    margin: 5rem auto 3rem;
    width: 90%;

    @include m.tablet {
        width: 60%;
    }

    .card-credencial {
        padding: 3rem;
        background-color: v.$blanco;
        border: 1px darken(v.$gris, 25) solid;
        box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
        border-radius: 8px;

        span {
            font-weight: 600;
        }

        p {
            text-align: center;
        }
    }
}

.container {
    width: 100%;
    padding: 1rem 0;

    .cards {
        display: flex;
        flex-direction: column;
        gap: 3rem;

        @include m.tablet {
            flex-direction: row;
        }

        pointer-events: none;
    }

    .card {
        max-width: 25rem;
        color: v.$gris;
        padding: 3rem;
        background-color: v.$negro;

        pointer-events: auto;

        transform: scale(1);
        opacity: 1;
        transition: all 150ms ease-in-out;

        display: flex;
        flex-direction: column;

        &:hover {
            .card-title {
                color: v.$verde;
            }
        }

        .card-title {
            position: relative;
            transition: color .3s;
            white-space: normal;

            &::before {
                content: "";
                height: 1rem;
                width: 1rem;
                border-radius: 50%;
                box-shadow: inset 0 0 3px v.$verde;
                background-color: transparent;

                position: absolute;
                right: 0;
            }
        }

        .date {
            color: v.$verde;
            font-size: 0.85rem;
            margin-bottom: 1.5rem;
        }

        .description {
            font-size: 0.9rem;
        }

        &:nth-child(even) {
            transform: translateY(8px);
        }

        &:nth-child(n) {
            transform: rotate(-5deg);
        }
    }
}

.cards:hover>.card:hover {
    transform: scale(1.15);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    z-index: 10;
}

.cards:hover>.card:not(:hover) {
    opacity: 0.5;
}